  props: {
    productData: Object,
    buttons: Object,
    StorerData: Object,
    width: Number,
    startNextVideo: Function,
  },
  methods: {
    handelSelect(idx) {
      idx = parseInt(idx - 1);
      // this.$router.push(this.button.routerPath);
      if (idx != 0) {
        this.$router.push(this.button.routerPath);
        EventBus.$emit("ShowTopNav", true);
      } else {
        EventBus.$emit("ShowAgeDialog");
      }

      // EventBus.$emit('SetTopNavIndex', idx);
    },
    handelEndVideo() {
      console.log("+++++++++++++++++ END VIDEO +++++++++++++++++++++++++++++");
      this.startNextVideo();
    },
  },
};
