<template>
  <div class="EntertainList" key="EntertainList">
    <div class="background"></div>
    <div class="backgroundOpacity"></div>

    <div class="contentContainer">
      <swiper
        :options="swiperOptionList"
        ref="mySwiper"
        key="productList"
        class="swiper-box"
        :class="{ extended: extended }"
      >
        <swiper-slide
          class="swiper-item"
          v-for="(p, idx) in entertain"
          :key="p.id"
        >
          <!-- <div
          class="productImage"
          :style="{ backgroundImage: 'url(' + p.background + ')' }"
        ></div> -->
          <Movie
            :entertain="p"
            :playButton="playButton"
            :extended="extended"
            :isActive="index === idx"
            :startVideo="onStartVideo"
          />
        </swiper-slide>
        <!-- <div
          v-if="isShowArows"
          class="swiper-button-prev swiper-button-white"
          slot="button-prev"
        ></div>
        <div
          v-if="isShowArows"
          class="swiper-button-next swiper-button-white"
          slot="button-next"
        ></div> -->
      </swiper>
      <div class="footerSwiper" :class="{ extended: extended }">
        <div class="backgroundCarusel"></div>
        <swiper
          :options="swiperFooter"
          ref="mySwiperFooter"
          key="footerList"
          class="footer-box"
        >
          <swiper-slide class="footer-item" v-for="p in entertain" :key="p.id">
            <div
              class="footerImage"
              :style="{ backgroundImage: 'url(' + p.background + ')' }"
            ></div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
    <!-- <NaigationTop :back="back" :extended="extended" /> -->
    <OfflineVideoPlayer
      :videoURL="entertain[index].videoURL"
      :close="() => (this.playVideo = false)"
      v-if="playVideo && extended"
    />
  </div>
</template>

<script>
import $ from "jquery";
import entertain_data from "../../public/dates/entertain_data";
import "swiper/css/swiper.css";
// import NaigationTop from "./NaigationTop";
import Movie from "./Movie.vue";
import OfflineVideoPlayer from "./OfflineVideoPlayer.vue";

import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import EventBus from "../main";

export default {
  name: "EntertainList",
  components: {
    Swiper,
    SwiperSlide,
    // NaigationTop,
    Movie,
    OfflineVideoPlayer,
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  data() {
    var _this = this;
    return {
      playVideo: false,
      videoPath: "",
      entertain: entertain_data.entertain,
      playButton: entertain_data.playButton,
      screenIndex: entertain_data.screenIndex,
      extended: false,
      isShowArows: true,
      index: 0,
      tempIndex: 0,
      winWidth: 0,
      winHeight: 0,
      swiperOptionList: {
        effect: "coverflow",
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 3,
        centerInsufficientSlides: true,
        spaceBetween: 540,
        coverflowEffect: {
          rotate: 0,
          stretch: 0,
          scale: 0.99,
          alpha: 0.6,
          depth: 0,
          modifier: 1,
          slideShadows: false,
        },
        // swiperOptionList: {
        // effect: "coverflow",
        // grabCursor: true,
        // centeredSlides: true,
        // slidesPerView: 3,
        // centerInsufficientSlides: true,
        // spaceBetween: 600,
        // coverflowEffect: {
        //   rotate: 0,
        //   stretch: 0,
        //   scale: 0.99,
        //   alpha: 0.6,
        //   depth: 5,
        //   modifier: 1,
        //   slideShadows: false,
        // },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        on: {
          click: function () {
            // console.log("mouse location:", e.clientX, e.clientY);
            // if (e.clientX > 330 && e.clientX < 955) {
            _this.extended = true;
            this.allowTouchMove = false;
            _this.isShowArows = false;
            EventBus.$emit("backEvent", _this.extended);
            // }
          },
          transitionEnd: function () {
            _this.index = _this.tempIndex;
            $(".background").css(
              "background-image",
              "url(" + _this.entertain[_this.tempIndex].background + ")"
            );
          },
          slideChange: function () {
            _this.tempIndex = _this.swiper.activeIndex;
          },
        },
      },

      swiperFooter: {
        slidesPerView: 4,
        spaceBetween: 120,
        speed: 5000,
        allowTouchMove: false,
        loop: true,
        freeMode: true,

        autoplay: {
          delay: 0,
        },
      },
    };
  },
  created() {
    this.windowResize();
  },
  methods: {
    back() {
      var _this = this;
      this.extended = false;
      setTimeout(function () {
        _this.isShowArows = true;
        _this.swiper.allowTouchMove = true;
      }, 600);
    },
    onStartVideo() {
      // this.entertain[this.index].videoURL
      this.playVideo = true;
    },
    windowResize: function() {
      this.winWidth = window.innerWidth;
      this.winHeight = window.innerHeight;
      if (this.winWidth == 1334)
      {
        this.swiperOptionList.spaceBetween = 600;
        this.swiperFooter.spaceBetween = 10;
      }
      else
      {
        this.swiperOptionList.spaceBetween = 540;
        this.swiperFooter.spaceBetween = 180;
      }
      console.log(this.winWidth , this.winHeight);
    }
  },
  mounted() {
    window.addEventListener("resize", this.windowResize);
    var _this = this;
    EventBus.$on("onBackEvent", function () {
      _this.extended = false;
      setTimeout(function () {
        _this.isShowArows = true;
        _this.swiper.allowTouchMove = true;
      }, 600);
    });

    EventBus.$emit("SetTopNavIndex", this.screenIndex);
    $(".background").css(
      "background-image",
      "url(" + this.entertain[0].background + ")"
    );
    this.index = this.swiper.activeIndex;
    console.log(this.entertain);
  },
};
</script>

<style lang="scss">
.EntertainList {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  position: fixed;
  .background {
    position: absolute;
    width: 103vw;
    height: 103vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(8px);
    -webkit-filter: blur(8px);
  }
  .backgroundOpacity {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .contentContainer {
    position: absolute;
    // background-color: aqua;
    width: 100vw;
    height: 640px;

    bottom: 0;
    .swiper-box {
      position: relative;
      transition: all 0.6s ease;
      width: 100%;
      height: 400px;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      // margin-top: 160px;
      // background-color: #ed77eda8;
      .swiper-item {
        text-align: center;
        font-size: 18px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        flex-direction: column;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
        // background-color: blue;
      }
    }
    .swiper-box.extended {
      height: 100% !important;
      .swiper-slide-prev {
        transform: scale(0) !important;
      }
      .swiper-slide-next {
        transform: scale(0) !important;
      }
    }

    .footerSwiper {
      transition: all 0.6s ease;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 25px;
      width: 100%;
      height: 200px;
      bottom: 0;

      // background-color: blue;
      .backgroundCarusel {
        // position: absolute;
        width: 95%;
        height: 100%;
        border-start-start-radius: 60px;
        border-end-end-radius: 60px;
        background-color: #e20074;
      }

      .footer-box {
        // position: relative;
        position: absolute;

        width: 100%;
        height: 200px;
        // margin-top: 140px;
        // margin-bottom: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
        // background-color: #ed77ed69;
        .footer-item {
          // height: 100%;
          text-align: center;
          font-size: 18px;
          // background: #fff;
          /* Center slide text vertically */
          display: -webkit-box;
          display: -ms-flexbox;
          display: -webkit-flex;
          display: flex;
          flex-direction: column;
          -webkit-box-pack: center;
          -ms-flex-pack: center;
          -webkit-justify-content: center;
          justify-content: center;
          -webkit-box-align: center;
          -ms-flex-align: center;
          -webkit-align-items: center;
          align-items: center;

          .footerImage {
            width: 300px;
            height: 170px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 20px;
            border: none;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            box-shadow: 5px 6px 10px 0px rgba(0, 0, 0, 0.5);
            // background-color: #0000ff;
          }
          //
        }
      }
    }
    .footerSwiper.extended {
      bottom: -240px;
    }
  }
}
.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: linear !important;
}

@media screen and (max-width: 1200px) {
  .EntertainList {
    .contentContainer {
      width: 100vw;
      height: 570px !important;
      .swiper-box {
        height: 350px !important;
      }
    }
  }
}
</style>
