<template>
  <div class="layout" key="layout">
    <!-- :style="{ backgroundImage: 'url(images/background.png)' }" -->
    <transition name="fade">
      <MainButton
        v-if="show"
        :productData="mainButtons[selected]"
        :buttons="buttons[selected]"
        :width="width"
        :startNextVideo="onStartNextVideo"
      />
    </transition>
    <NavigationMain :mainButtons="buttons" :onSelect="onSelectProduct" :activeButton="selected" />
  </div>
</template>

<script>
import all_data from "../../public/dates/main_data";
import nav_data from "../../public/dates/nav_data";

import MainButton from "./MainButton";
import NavigationMain from "./NavigationMain";
import EventBus from "../main";

export default {
  name: "Layout",
  components: {
    MainButton,
    NavigationMain,
  },
  props: {},
  data() {
    return {
      width: window.innerWidth,
      height: window.innerHeight,
      mainButtons: all_data.mainButtons,
      buttons: nav_data.buttons,
      selected: 0,
      show: true,
      changeActiveButton: Function,
    };
  },
  methods: {
    onSelectProduct(index) {
      this.show = false;
      this.selected = index;
      setInterval(() => (this.show = true), 800);
    },
    onStartNextVideo(){
      console.log(this.buttons.length + " - " + this.selected);
      this.show = false;
      if ((this.buttons.length -1) > this.selected)
      {
        this.selected += 1;
      }
      else
      {
        this.selected = 0;
      }

      setInterval(() => (this.show = true), 800);
    }
  },
  mounted () {
    EventBus.$emit('ShowTopNav', false);
  },
};
</script>

