<template>
  <div id="app" @click="resetTimer">
    <div id="container" class="shaderContainer"></div>
    <transition name="fade">
      <router-view></router-view>
    </transition>
    <NaigationTop />
    <AgeDialog />
    <!-- <NaigationTop :back="back" :extended="extended" /> -->
    <div class="log" v-if="isdevelopment">{{ getAppInfo() }}</div>
  </div>
</template>

<script>
import all_data from "../public/dates/main_data";
import * as Three from "three";
// import Stats from './stats.module.js';
import NaigationTop from "./components/NaigationTop";
import AgeDialog from "./components/AgeDialog";

// import Screensaver from "./components/Screensaver";
// import Layout from "./components/Layout";
// import EntertainList from "./components/EntertainList";

export default {
  name: "App",
  components: {
    NaigationTop,
    AgeDialog,
    // Screensaver,
    // Layout,
    // EntertainList,
  },
  // timers: {
  //   screensaverTimer: {
  //     time: all_data.screensaver.timer * 1000,
  //     autostart: true,
  //     repeat: true,
  //   },
  // },
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      uniforms: null,
      select: 0,
      screensaverUrls: [],
      screensaver: all_data.screensaver,
      name: all_data.name,
      isdevelopment: all_data.mode === "development",
      productsData: all_data.mainButtons,
      width: 1280,
      height: 800,
    };
  },
  created() {
    window.addEventListener("resize", this.handelWindowResize);
    this.handelWindowResize();
    // document.title = this.name;
    // this.screensaverTimer();
  },
  mounted() {
    this.init();
    this.animate();
    this.onWindowResize();
  },
  methods: {
    init: function () {
      // var stats = new Stats();
      // document.body.appendChild(stats.dom);
      // requestAnimationFrame(function loop() {
      //   stats.update();
      //   requestAnimationFrame(loop);
      // });

      window.Three = Three;
      // var _this = this;
      this.container = document.getElementById("container");

      this.camera = new Three.OrthographicCamera(-1, 1, 1, -1, 0, 1);

      this.scene = new Three.Scene();

      const geometry = new Three.PlaneBufferGeometry(2, 2);

      const fragmentShader = `
    	#include <common>

    	uniform vec3 iResolution;
    	uniform float iTime;
    	uniform sampler2D iChannel0;

    	#define SPLIT 6.28318530718

    	#define TILING_FACTOR 1.0
    	#define MAX_ITER 2


      const float gStep = .6;
      const vec4 lineColor = vec4(0.8,0.4,0.5, 1.0);
      const vec4[] bgColors = vec4[]
          (
              lineColor * 0.1,
              
              vec4(0.773,0.,0.396, 1)
              );
      vec4 hash42(vec2 p)
      {
        vec4 p4 = fract(vec4(p.xyxy) * vec4(.1031, .1030, .0973, .1099));
          p4 += dot(p4, p4.wzxy+33.33);
          return fract((p4.xxyz+p4.yzzw)*p4.zywx);
      }

    	void mainImage( out vec4 o, in vec2 fragCoord )
    	{
    		vec2 uv = fragCoord/iResolution.xy;
        vec2 uvo = uv-.5;
        uv.x *= iResolution.x / iResolution.y;
        uv *= 19.;
        uv += sin((vec2(uv.y, -uv.x)+iTime*.5)*4.)*.3;
        uv += 14.+iTime;
        
        vec2 id = floor(uv);
        float s = 1.;
        
        for (float i = 0.;i<1.;i+=gStep)
        {
            s = -s;
            uv += sin((vec2(uv.y,s*uv.x) + iTime*.001));
            uv *= (i+.4);
            
            id = floor(uv);
            vec2 p =  uv-id;
            vec4 h = bgColors[1];
            
            float d = .707-length((p-.25));
            float d2 = step(mix(.15,.15,h.x), d);
            
            o = d2 * h;
        }
        
        o *= 1.-dot(uvo,1.5*uvo);
    	}

    	varying vec2 vUv;

    	void main() {
    		mainImage(gl_FragColor, vUv * iResolution.xy);
    	}
    	`;
      const vertexShader = `
    		varying vec2 vUv;
    		void main() {
    		vUv = uv;
    		gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
    		}
    	`;

      const loader = new Three.TextureLoader();
      const texture = loader.load(
        "https://threejsfundamentals.org/threejs/resources/images/bayer.png"
      );
      texture.minFilter = Three.NearestFilter;
      texture.magFilter = Three.NearestFilter;
      texture.wrapS = Three.RepeatWrapping;
      texture.wrapT = Three.RepeatWrapping;
      const uniforms = {
        iTime: { value: 0 },
        iResolution: { value: new Three.Vector3(1, 1, 1) },
        iChannel0: { value: texture },
      };
      this.uniforms = uniforms;
      //const unif = this.uniforms;
      const materialShader = new Three.ShaderMaterial({
        vertexShader,
        fragmentShader,
        uniforms,
      });

      const mesh = new Three.Mesh(geometry, materialShader);
      this.scene.add(mesh);

      this.renderer = new Three.WebGLRenderer();
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.container.appendChild(this.renderer.domElement);

      this.onWindowResize();

      window.addEventListener("resize", this.onWindowResize, false);
    },

    animate: function () {
      requestAnimationFrame(this.animate);
      this.uniforms["iTime"].value = performance.now() / 1000;
      // this.cube.rotation.x += 0.01
      // this.cube.rotation.y += 0.01

      this.renderer.render(this.scene, this.camera);
    },

    onWindowResize: function () {
      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },

    // screensaverTimer() {
    //   if (this.screensaver.cancel) {
    //     this.goToProducts(1);
    //     return;
    //   }
    //   this.resetUrls();
    //   this.goToScreensaver();
    // },
    // resetUrls() {
    //   if (this.width <= 1030) {
    //     this.screensaverUrls = [...this.screensaver.q1_4_3_urls];
    //   } else if (this.width > 1070 && this.width <= 1300) {
    //     this.screensaverUrls = [...this.screensaver.h_9_16_urls];
    //   } else if (this.width > 1300) {
    //     this.screensaverUrls = [...this.screensaver.q2_16_9_urls];
    //   }
    // },
    resetTimer() {
      console.log("ResetTime");
      // this.goToProducts(1);
      // this.$timer.restart("screensaverTimer");
    },

    handelWindowResize() {
      this.select = 0;
      this.width = window.innerWidth;
      this.height = window.innerHeight;
    },
    goToProducts(index) {
      this.select = index;
    },

    goToScreensaver() {
      this.select = 0;
    },
    getAppInfo() {
      const { name, version } = this.get_browser();
      return (
        this.width +
        "px X " +
        this.height +
        "px  Browser: " +
        name +
        "\n  version: " +
        version
      );
    },
    get_browser() {
      var ua = navigator.userAgent,
        tem,
        M =
          ua.match(
            /(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i
          ) || [];
      if (/trident/i.test(M[1])) {
        tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
        return { name: "IE", version: tem[1] || "" };
      }
      if (M[1] === "Chrome") {
        tem = ua.match(/\bOPR|Edge\/(\d+)/);
        if (tem != null) {
          return { name: "Opera", version: tem[1] };
        }
      }
      M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, "-?"];
      if ((tem = ua.match(/version\/(\d+)/i)) != null) {
        M.splice(1, 1, tem[1]);
      }
      return {
        ua,
        name: M[0],
        version: M[1],
      };
    },
  },
  watch: {
    $router(to, from) {
      console.log(to.name, from);
    },
  },
};
</script>

