<template>
  <div class="Disney" key="Disney">
    <OfflineVideoPlayer
      :videoURL="disney.videoURL"
      :close="endVideo"
      v-if="playVideo"
    />
    
  </div>
</template>

<script>
// import $ from "jquery";
import disney_data from "../../public/dates/disney_data";
import EventBus from "../main";
import OfflineVideoPlayer from "./OfflineVideoPlayer.vue";

export default {
  name: "Disney",
  components: {
    OfflineVideoPlayer
  },
  data() {
    // var _this = this;
    return {
      disney: disney_data.disney,
      playVideo: true,
    };
  },
  methods: {
    endVideo(){
      this.playVideo = false;
      this.$router.go(-1);
    }
  },
  mounted () {
    EventBus.$emit('SetTopNavIndex', this.disney.screenIndex);
  },
};
</script>

