<template>
  <div class="topnavbar" v-if="activeNav">
    <button
      class="backButton"
      @click="handelBack"
      :style="{ backgroundImage: 'url(' + backButtons + ')' }"
    ></button>
    <!-- :class="{ extended: extended }" -->

    <!-- <button
      v-for="(buttons, idx) in buttons"
      :key="buttons.id"
      @click="handelSelect(buttons.routerPath)"
      class="buttonContainer"
      :class="{ active: buttonIndex === idx }"
    > -->
    <!-- <div class="buttonContainer">
      <button
        v-for="buttons in buttons"
        :key="buttons.id"
        @click="handelSelect(buttons.routerPath)"
