          :style="{ backgroundImage: 'url(' + buttons.iconPath + ')' }"
        ></div>
      </button>
    </div> -->

    <div class="buttonContainer">
      <button
        v-for="(buttons, idx) in buttons"
        :key="buttons.id"
        @click="handelSelect(buttons.routerPath, idx)"
        class="buttonNav"
        :class="{ active: activeButton === idx }"
      >
        <div class="buttonBackground">
          <div
            class="icon"
            :style="{ backgroundImage: 'url(' + buttons.iconPath + ')' }"
          ></div>
        </div>
      </button>
    </div>
    <!-- <router-link to="/"> -->
    <button
      class="homeButton"
      @click="handelHome"
      :style="{ backgroundImage: 'url(' + homeButtons + ')' }"
    ></button>
    <!-- </router-link> -->
  </div>
</template>

<script>
import EventBus from "../main";
import nav_data from "../../public/dates/nav_data";
// import $ from "jquery";

export default {
  name: "topnavbar",
  components: {
    // Swiper,
    // SwiperSlide,
  },
  // directives: {
  //   swiper: directive,
  // },
  // props: {
  //   back: Function,

  //   // onSelect: Function,
  //   // mainButtons: Array,
  // },
  mounted() {
    var _this = this;
    EventBus.$on("ShowTopNav", function (active) {
      _this.activeNav = active;
    });
    EventBus.$on("SetTopNavIndex", function (index) {
      _this.activeButton = index;
    });

    EventBus.$on("backEvent", function (value) {
      _this.extended = value;
    });

    // this.isLoaded = true;
    // this.startMenuAnimation(98, true);
    // eventBus.$on("mainButtonEnableEvent", () => {
    //   this.isDisabled = false;
    // });
  },
  data() {
    return {
      buttons: nav_data.buttons,
      homeButtons: nav_data.homeButtons,
      backButtons: nav_data.backButtons,
      activeButton: 0,
      activeNav: false,
      extended: false,
      // mainButtonIndex: 0,
    };
  },
  methods: {
    handelSelect(routerPath, idx) {
      if (idx != 0) {
        this.$router.push(routerPath);
      } else {
        EventBus.$emit("ShowAgeDialog");
      }
      this.extended = false;
    },
    handelBack() {
      if (this.extended) {
        this.extended = false;
        EventBus.$emit("onBackEvent");
      } else {
        this.$router.go(-1);
      }
    },
    handelHome() {
      this.$router.push("/");
      this.activeNav = false;
      this.extended = false;
    },
  },
};
</script>

