<template>
  <div class="AgeDialog" key="AgeDialog" v-if="isActive">
    <button
      class="Container"
      :class="{ active: isActive }"
      @click="handelCancelButton()"
    >
      <div
        class="AtentionImage"
        :style="{ backgroundImage: 'url(' + age_dialog.imagePath + ')' }"
      />
      <div class="Dialog">{{ age_dialog.dialog }}</div>
      <div class="ButtonsContainer">
        <button class="YesButton" @click="handelYesButton()">
          {{ age_dialog.yesButton }}
        </button>
        <button class="NoButton" @click="handelNoButton()">
          {{ age_dialog.noButton }}
        </button>
      </div>
    </button>
  </div>
</template>

<script>
import age_dialog_data from "../../public/dates/age_dialog_data";
import EventBus from "../main";

export default {
  data() {
    return {
      age_dialog: age_dialog_data.age_dialog,
      isActive: false,
    };
  },
  mounted() {
    var _this = this;
    EventBus.$on("ShowAgeDialog", function () {
      _this.isActive = true;
      // setTimeout(function () {
      //   _this.isActive = false;
      // }, 10000);
    });
  },
  methods: {
    handelYesButton() {
      this.isActive = false;
      this.$router.push(this.age_dialog.yesRouterPath);
      EventBus.$emit("ShowTopNav", true);
    },
    handelNoButton() {
      this.isActive = false;
      this.$router.push(this.age_dialog.noRouterPath);
      EventBus.$emit("ShowTopNav", true);
      EventBus.$emit("SetTopNavIndex", this.age_dialog.noScreenIndex);
    },
    handelCancelButton() {
      this.isActive = false;
    },
  },
};
</script>

<style lang="scss">
.AgeDialog {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.4);
  .Container {
    transition: all 0.6s ease;
    width: 80vw;
    height: 80vh;
    background-color: rgba(0, 0, 0, 0.8);
    border-color: transparent;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .AtentionImage {
      width: 378px;
      height: 308px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      margin-bottom: 40px;
    }
    .Dialog {
      width: 100%;
      color: white;
      font-size: 4.2em;
      font-family: "TeleNeo-ExtraBold";
      text-align: center;
      margin-bottom: 40px;
    }
    .ButtonsContainer {
      .YesButton {
        color: white;
        font-size: 2.5em;
        font-family: "TeleNeo-Regular";
        width: 180px;
        height: 60px;
        background-color: transparent;
        border-color: white;
        border-radius: 10px;
        border-style: solid;
        border-width: 2px;
        margin-right: 50px;
      }
      .NoButton {
        color: white;
        font-size: 2.5em;
        font-family: "TeleNeo-Regular";
        width: 180px;
        height: 60px;
        background-color: transparent;
        border-color: white;
        border-radius: 10px;
        border-style: solid;
        border-width: 2px;
      }
    }
  }
}
</style>