<template>
  <div class="player">
    <video-player
      ref="videoPlayer"
      class="vjs-custom-skin"
      :options="playerOptions"
      @play="onPlayerPlay($event)"
      @ready="onPlayerReady($event)"
    />
    <button class="touchArea" @click="handelTouchArea()"></button>
    <div class="closeButtonContainer">
      <button
        class="closeButton"
        :style="{
          backgroundImage: 'url(images/video_back_button.png)',
        }"
        @click="handelCloseButton()"
        :class="{ toogleClose: toogleButton }"
      ></button>
    </div>
  </div>
</template>

<script>
import VideoPlayer from "@/components/VideoPlayer.vue";
import EventBus from "../main";
export default {
  name: "home",
  props: {
    close: Function,
    src: String,
    controls: Boolean,
    playerOptions: Object,
  },
  components: {
    VideoPlayer,
  },
  data() {
    return {
      toogleButton: false,
    };
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    },
  },
  methods: {
    handelTouchArea() {
      var _this = this;
      this.toogleButton = true;
      setTimeout(function () {
        _this.toogleButton = false;
      }, 5000);
    },
    handelCloseButton(){
      this.close();
      EventBus.$emit('ShowTopNav', true);
    },
    onPlayerPlay(player) {
      console.log(player);
    },
    onPlayerReady(player) {
      console.log(player);
      this.player.play();
    },
    playVideo: function (source) {
      const video = {
        withCredentials: false,
        type: "application/x-mpegurl",
        src: source,
      };
      this.player.reset(); // in IE11 (mode IE10) direct usage of src() when <src> is already set, generated errors,
      this.player.src(video);
      // this.player.load()
      this.player.play();
    },
    switchPlayer: function () {
      this.playVideo(this.src);
    },
  },
  mounted() {
    this.playVideo(this.$props.src);
    EventBus.$emit('ShowTopNav', false);
  },
};
</script>

<style lang="scss">
.player {
  /* position: absolute !important;
  width: 100%;
  height: 100%; */
  transition: all 0.6s ease;
  top: 0;
  left: 0;

  background-color: black;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  .vjs-custom-skin {
    width: 100% !important;
    height: 100% !important;
    .video-js {
      width: 100% !important;
      height: 100% !important;
    }
  }
  .touchArea {
    position: absolute;
    width: 100vw;
    height: 100vh;
    border-color: transparent;
    background-color: transparent;
    // background-color: transparent;
  }
  .closeButtonContainer {
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 100;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    // background-color: turquoise;
    .closeButton {
      transition: all 0.6s ease;
      width: 0;
      height: 0;
      border-radius: 50%;
      border-color: transparent;
      background-color: transparent;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
    .closeButton.toogleClose {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
