<template>
  <div class="Music" key="Music">
    <div class="LeftSide">
      <div class="title">{{ music.title }}</div>
      <div class="descriptionTitle1">{{ music.descriptionTitle1 }}</div>
      <div class="description1">{{ music.description1 }}</div>
    </div>
    <div class="RightSide">
      <button class="PictureContainer" @click="handelPlayVideo()">
        <div
          class="SportPicture"
          :style="{
            backgroundImage:
              'url(' +
              buttons[selectionButtonIndex].list[selectionListIndex].imagePath +
              ')',
          }"
        >
          <div
            class="Play"
            :style="{ backgroundImage: 'url(' + music.playButton + ')' }"
          ></div>
        </div>
        <div class="videoText">
          <div class="videoDescrption1">
            {{
              buttons[selectionButtonIndex].list[selectionListIndex]
                .videoDescrption1
            }}
          </div>
          <div class="videoDescrption2">
            {{
              buttons[selectionButtonIndex].list[selectionListIndex]
                .videoDescrption2
            }}
          </div>
        </div>
      </button>
    </div>
    <div class="fotter" :class="{ active: toogleArow }">
      <div class="arrowContainer">
        <button class="buttonArow" @click="handeltoogleArow()">
          <div
            class="icon"
            :style="{ backgroundImage: 'url(' + music.arowButton + ')' }"
          ></div>
        </button>
      </div>

      <div class="buttonSelection">
        <button
          v-for="(selectionButton, idx) in buttons"
          :key="selectionButton.id"
          @click="handelSelect(idx)"
          class="selectionButton"
          :class="{ active: selectionButtonIndex === idx }"
        >
          {{ selectionButton.label }}
        </button>
      </div>
      <swiper
        :options="swiperOptionList"
        ref="mySwiper"
        key="productList"
        class="swiper-box"
      >
        <swiper-slide
          class="swiper-item"
          v-for="(p, idx) in buttons[selectionButtonIndex].list"
          :key="p.id"
        >
          <div
            class="productImage"
            :style="{ backgroundImage: 'url(' + p.imagePath + ')' }"
          >
            <div class="label">
              {{ buttons[selectionButtonIndex].list[idx].videoDescrption1 }}
            </div>
          </div>
        </swiper-slide>
        <div class="swiper-scrollbar" slot="scrollbar"></div>
      </swiper>
    </div>
    <!-- <NaigationTop /> -->
    <OnlineVideoPlayer
      v-if="show"
      :close="() => (this.show = false)"
      :src="buttons[selectionButtonIndex].list[selectionListIndex].videoURL"
      :playerOptions="{
        muted: false,
        autoplay: false,
        controls: false,
        disablePictureInPicture: true,
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
        },
      }"
    />

    <!-- <OldVideoPlayer  :videoURL="buttons[selectionButtonIndex].list[selectionListIndex].videoURL" :show="show" :close="()=>this.show=false"></OldVideoPlayer> -->
  </div>
</template>

<script>
// import $ from "jquery";
import music_data from "../../public/dates/music_data";
import OnlineVideoPlayer from "./OnlineVideoPlayer.vue";
import "swiper/css/swiper.css";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import EventBus from "../main";

export default {
  name: "Music",
  components: {
    Swiper,
    SwiperSlide,
    // NaigationTop,
    // OldVideoPlayer,
    OnlineVideoPlayer,
  },
  props: {
    onSelect: Function,
  },
  data() {
    var _this = this;
    return {
      show: false,
      toogleArow: false,
      selectionButtonIndex: 0,
      selectionListIndex: 0,
      music: music_data.music,
      buttons: music_data.buttons,

      swiperOptionList: {
        // effect: "coverflow",
        // grabCursor: true,
        // centeredSlides: true,
        direction: "horizontal",
        slidesPerView: 4,
        freeMode: true,
        scrollbar: {
          el: ".swiper-scrollbar",
        },
        // spaceBetween: 40,
        // coverflowEffect: {
        //   rotate: 0,
        //   stretch: 0,
        //   scale: 0.99,
        //   alpha: 0.6,
        //   depth: 5,
        //   modifier: 1,
        //   slideShadows: false,
        // },
        navigation: {
          nextEl: ".swiper-button-next1",
          prevEl: ".swiper-button-prev1",
        },
        on: {
          // beforeDestroy: function () {
          //   throw new Error(
          //     "This is not an error. This is just to abort javascript"
          //   );
          // },
          click: function () {
            _this.selectionListIndex =
              _this.$refs.mySwiper.$swiper.clickedIndex;
            // console.log("PLM " + _this.selectionListIndex);
          },
        },
      },
    };
  },
  methods: {
    handelSelect(idx) {
      this.selectionButtonIndex = idx;
      this.onSelect(idx);
    },
    handeltoogleArow() {
      this.toogleArow = !this.toogleArow;
      console.log(this.toogleArow);
    },
    handelPlayVideo() {
      this.show = !this.show;
      this.toogleArow = true;
    },
  },
  mounted() {
    EventBus.$emit('SetTopNavIndex', this.music.screenIndex);
    var _this = this;
    setTimeout(function () {
      _this.toogleArow = true;
    }, 1200);
  },
};
</script>

<style lang="scss">
.Music {
  white-space: pre-line;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: relative;
  // background-repeat: no-repeat;
  // background-size: cover;
  // background-position: center;
  .LeftSide {
    //  background-color: blue;
    overflow: hidden;
    position: absolute;
    left: 0;
    margin-top: 150px;
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    width: 50vw;
    height: 650px;
    .title {
      text-align: left;
      font-size: 4.2em;
      font-family: "TeleNeo-ExtraBold";
      width: 100%;
      color: #fff;
      margin-bottom: 40px;
    }
    .descriptionTitle1 {
      text-align: left;
      font-size: 2.5em;
      font-family: "TeleNeo-ExtraBold";
      width: 100%;
      color: #fff;
      margin-bottom: 20px;
    }
    .description1 {
      text-align: left;
      font-size: 1.5em;
      font-family: "TeleNeo-Regular";
      width: 100%;
      color: #fff;
      margin-bottom: 40px;
    }
  }

  .RightSide {
    overflow: hidden;
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50vw;
    height: 100vh;
    .PictureContainer {
       width: 450px;
      height: 337.5px;
      display: flex;
      border: transparent;
      border-color: transparent;
      border-end-end-radius: 60px;
      background-color: #e20074;
      box-shadow: 6px 8px 10px 0px rgba(0, 0, 0, 0.4);
      .SportPicture {
        margin-top: -4px;
        width: 450px;
        height: 256.5px;
        display: flex;
        align-items: flex-end;

        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        .Play {
          display: flex;
          margin-left: 370px;
          margin-bottom: 20px;
          // bottom: 10px;
          width: 50px;
          height: 50px;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
        }
      }
      .videoText {
        position: absolute;
        // background-color: rgba(126, 252, 0, 0.637);
        justify-content: center;
        // align-items: center;
        flex-direction: column;
        width: 450px;
        height: 82px;
        padding-left: 20px;
        margin-top: 256px;
        color: #fff;
        font-size: 1.5em;
        display: flex;
        .videoDescrption1 {
          // font-size: 0.8em;
          text-align: left;

          font-family: "TeleNeo-ExtraBold";
        }
        .videoDescrption2 {
          // font-size: 0.8em;
          text-align: left;
          font-family: "TeleNeo-Regular";
        }
      }
    }
  }
  .fotter {
    transition: all 0.6s ease;
    box-shadow: 0px -8px 10px 0px rgba(0, 0, 0, 0.4);
    background-color: #e20074;
    position: absolute;
    width: 100%;
    height: 240px;
    bottom: 0;
    .arrowContainer {
      position: absolute;
      // z-index: 5;
      width: 100%;
      height: 60px;
      justify-content: center;
      display: flex;
      align-self: center;
      align-items: center;
      .buttonArow {
        box-shadow: 0px -8px 5px 0px rgba(0, 0, 0, 0.4);
        position: absolute;
        top: -25px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #e20074;
        border-color: transparent;
        .icon {
          transition: all 0.6s ease;
          width: 70%;
          height: 70%;
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
        }
      }
    }

    .buttonSelection {
      position: absolute;
      // z-index: 10;
      width: auto;
      height: 60px;
      justify-content: flex-start;
      display: flex;
      align-self: center;
      align-items: center;
      flex-direction: row;

      margin: 0;
      margin-left: 50px;

      .selectionButton {
        transition: all 0.1s ease;
        font-family: "TeleNeo-Regular";
        font-weight: normal;
        font-size: 2.2em;
        color: white;
        margin-right: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        border-color: transparent;
        background-color: transparent;
        width: auto;
        height: 40px;
      }
      .selectionButton.active {
        // font-family: "TeleNeo-ExtraBold";
        font-weight: bold;
      }
    }
    .swiper-box {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 190px;
      display: flex;
      justify-content: center;
      align-items: center;
      // top: 228px;
      .swiper-item {
        // height: 100%;
        text-align: center;
        font-size: 18px;
        // background: #fff;
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        flex-direction: column;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;

        .productImage {
          width: 21vw;
          height: 19vh;
          display: flex;
          justify-content: flex-start;
          align-items: flex-end;
          border-radius: 30px;
          border: none;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
          box-shadow: 6px 8px 10px 0px rgba(0, 0, 0, 0.4);
          // background-color: #0000ff;
          .label {
            color: white;
            font-family: "TeleNeo-Regular";
            font-size: 1.2em;
            margin-left: 12px;
            margin-bottom: 10px;
            text-shadow: 2px 2px 4px #000000;
          }
        }
        //
      }
      .swiper-scrollbar {
        // background: transparent;
        // height: 3px;
        .swiper-scrollbar-drag {
          background: rgba(255, 255, 255, 0.7);
        }
      }
    }
  }
  .fotter.active {
    bottom: -220px;
    .arrowContainer {
      .buttonArow {
        .icon {
          margin-bottom: 10px;
          transform: rotate(180deg);
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .swiper {
    height: 520px !important;
  }
  .RightSide {
    width: 50vw;
    height: 100vh;
    .PictureContainer {
      width: 400px !important;
      height: 300px !important;
      
      .SportPicture {
        top: 0;
        width: 400px !important;
        height: 228px !important;
        
        .Play {
          margin-left: 330px !important;
          margin-bottom: 20px !important;
          
        }
      }
      .videoText {
        width: 400px !important;
        height: 72px !important;
        margin-top: 228px !important;
        
      }
    }
  }
}
</style>
