<template>
  <div class="Sport" key="Sport">
    <swiper class="swiper" :options="swiperOption">
      <swiper-slide class="text">
        <div class="LeftSide">
          <div class="title">{{ sport.title }}</div>
          <div class="descriptionTitle1">{{ sport.descriptionTitle1 }}</div>
          <div class="description1">{{ sport.description1 }}</div>
          <div class="descriptionTitle2">{{ sport.descriptionTitle2 }}</div>
          <div class="description2">{{ sport.description2 }}</div>
        </div>
      </swiper-slide>
      <div class="swiper-scrollbar" slot="scrollbar"></div>
    </swiper>
    <div class="RightSide">
      <button class="PictureContainer" @click="handelPlayVideo()">
        <div
          class="SportPicture"
          :style="{ backgroundImage: 'url(' + sport.imagePath + ')' }"
        >
          <div
            class="Play"
            :style="{ backgroundImage: 'url(' + sport.playButton + ')' }"
          ></div>
        </div>
        <div class="videoText">
          <div class="firstLine">
            <div class="videoDescrption1">{{ sport.videoDescrption1 }}</div>
            <div class="videoDescrption2">{{ sport.videoDescrption2 }}</div>
          </div>
          <div class="videoDescrption3">{{ sport.videoDescrption3 }}</div>
        </div>
      </button>
    </div>
    <!-- <NaigationTop /> -->
    <OfflineVideoPlayer
      :videoURL="sport.videoURL"
      :close="() => (this.playVideo = false)"
      v-if="playVideo"
    />
  </div>
</template>

<script>
// import $ from "jquery";
import sport_data from "../../public/dates/sport_data";
import OfflineVideoPlayer from "./OfflineVideoPlayer.vue";
import EventBus from "../main";
import "swiper/css/swiper.css";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";

export default {
  name: "Sport",
  components: {
    Swiper,
    SwiperSlide,
    OfflineVideoPlayer,
  },
  computed: {
    getScreenIndex() {
      return this.screenIndex;
    },
  },
  data() {
    // var _this = this;
    return {
      sport: sport_data.sport,
      screenIndex: 0,
      playVideo: false,
      swiperOption: {
        direction: "vertical",
        slidesPerView: "auto",
        freeMode: true,
        scrollbar: {
          el: ".swiper-scrollbar",
        },
      },
    };
  },
  methods: {
    handelPlayVideo() {
      // this.entertain[this.index].videoURL
      this.playVideo = true;
    },
  },
  mounted() {
    EventBus.$emit("SetTopNavIndex", this.sport.screenIndex);
  },
};
</script>

