<template>
  <div class="KidsMenu" key="KidsMenu">
    <div class="bottonsContainer">
      <button
        class="entertainButton"
        @click="handelSelect(kids.entertainRouterPath)"
        :style="{ backgroundImage: 'url(' + kids.entertainButton + ')' }"
      ></button>
      <button
        class="gamesButton"
        @click="handelSelect(kids.gamesRouterPath)"
        :style="{ backgroundImage: 'url(' + kids.gamesButton + ')' }"
      ></button>
    </div>
    <!-- <NaigationTop /> -->
  </div>
</template>

<script>
// import $ from "jquery";
import kids_menu_data from "../../public/dates/kids_menu_data";
import EventBus from "../main";

export default {
  name: "KidsMenu",
  components: {
    // NaigationTop,
  },
  data() {
    // var _this = this;
    return {
      kids: kids_menu_data.kids,
    };
  },
  methods: {
    handelSelect(routerPath) {
      this.$router.push(routerPath);
    },
  },
  mounted() {
    EventBus.$emit("SetTopNavIndex", this.kids.screenIndex);
  },
};
</script>

<style lang="scss">
.KidsMenu {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  .bottonsContainer {
    position: absolute;
    width: 100vw;
    height: auto;
    justify-content: center;
    display: flex;
    align-self: center;
    align-items: center;
    flex-direction: row;
    margin-top: 100px;
    .entertainButton {
      width: 550px;
      height: 550px;
      background: transparent;
      border: transparent;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      margin-right: 50px;
    }
    .gamesButton {
      width: 550px;
      height: 550px;
      background: transparent;
      border: transparent;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
  }
}

@media screen and (max-width: 1200px) {
  .KidsMenu {
    .bottonsContainer {
      .entertainButton {
        width: 480px !important;
        height: 480px !important;
      }
      .gamesButton {
        width: 480px !important;
        height: 480px !important;
      }
    }
  }
}
</style>
