<template>
  <div class="VideoPlayer" key="VideoPlayer">
    <!-- <video autoplay v-on:ended="handelCloseButton">
      <source :src="videoURL" />
    </video> -->

    <video
      ref="video"
      :src="videoURL"
      autoplay
      muted
      v-on:ended="handelCloseButton"
    ></video>

    <button class="touchArea" @click="handelTouchArea()"></button>
    <div class="closeButtonContainer">
      <button
        class="closeButton"
        :style="{
          backgroundImage: 'url(images/video_back_button.png)',
        }"
        @click="handelCloseButton()"
        :class="{ toogleClose: toogleButton }"
      ></button>
    </div>
  </div>
</template>

<script>
// import $ from "jquery";
import entertain_data from "../../public/dates/entertain_data";
import EventBus from "../main";

export default {
  name: "VideoPlayer",
  components: {},
  props: {
    close: Function,
    videoURL: String,
  },
  data() {
    // var _this = this;
    return {
      // isMuted: true,
      toogleButton: false,
      entertain: entertain_data.entertain,
    };
  },
  methods: {
    handelTouchArea() {
      var _this = this;
      this.toogleButton = true;
      setTimeout(function () {
        _this.toogleButton = false;
      }, 5000);
    },
    handelCloseButton() {
      this.close();
      EventBus.$emit("ShowTopNav", true);
    },
  },
  mounted() {
    EventBus.$emit("ShowTopNav", false);
    var vid = this.$refs.video;
      vid.muted = false;
  },
};
</script>

<style lang="scss">
.VideoPlayer {
  z-index: 10;
  background-color: black;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  video {
    background-color: black;
    width: 100%;
    height: 100%;
  }
  .touchArea {
    position: absolute;
    width: 100vw;
    height: 100vh;
    border-color: transparent;
    background-color: transparent;
  }
  .closeButtonContainer {
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 100;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: transparent;
    .closeButton {
      transition: all 0.6s ease;
      width: 0;
      height: 0;
      border-radius: 50%;
      border-color: transparent;
      background-color: transparent;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
    .closeButton.toogleClose {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
