import Vue from "vue";
import Router from "vue-router";
import MainMenu from "./components/MainMenu.vue";
import EntertainList from "./components/EntertainList.vue";
import KidsList from "./components/KidsList.vue";
// import Movie from "./components/Movie.vue";
import Sport from "./components/Sport.vue";
import Music from "./components/Music.vue";
import KidsMenu from "./components/KidsMenu.vue";
import KidsGames from "./components/KidsGames.vue";
import Disney from "./components/Disney.vue";
import VirtualRegal from "./components/VirtualRegal.vue";
Vue.use(Router);

export default new Router({
  routes: [

    {
      path: "/",
      name: "MainMenu",
      component: MainMenu,
    },
    // {
    //   path: "/Movie",
    //   name: "Movie",
    //   component: Movie,
    // },
    {
      path: "/EntertainList",
      name: "EntertainList",
      component: EntertainList,
    },
    {
      path: "/Sport",
      name: "Sport",
      component: Sport,
    },
    {
      path: "/Music",
      name: "Music",
      component: Music,
    },
    {
      path: "/KidsMenu",
      name: "KidsMenu",
      component: KidsMenu,
    },
    {
      path: "/KidsList",
      name: "KidsList",
      component: KidsList,
    },
    {
      path: "/KidsGames",
      name: "KidsGames",
      component: KidsGames,
    },
    {
      path: "/Disney",
      name: "Disney",
      component: Disney,
    },
    {
      path: "/VirtualRegal",
      name: "VirtualRegal",
      component: VirtualRegal,
    },
  ],
});