<template>
  <div class="Movie" key="Movie" :class="{ extended: extended }">
    <swiper class="swiper" :options="swiperOption" ref="mySwiper">
      <!-- <div
        class="parallax-bg"
        slot="parallax-bg"
        data-swiper-parallax="-23%"
      ></div> -->
      <div
        class="parallax-bg"
        slot="parallax-bg"
        :class="{ swiperUp: swiperPos }"
      >
        <div class="magentaBox" v-if="isActive">
          <div class="firstLine">
            <div class="seasons" v-if="!entertain.seasons.isEmpty()">
              {{ entertain.seasons }}
            </div>
            <div class="folgen" v-if="!entertain.folgen.isEmpty()">
              {{ entertain.folgen }}
            </div>
          </div>
          <div class="secondLine">
            <div class="gen" v-if="!entertain.gen.isEmpty()">
              {{ entertain.gen }}
            </div>
          </div>
        </div>
        <div
          class="itemBackground"
          :style="{ backgroundImage: 'url(' + entertain.background + ')' }"
        >
          <button
            class="Play"
            v-if="isActive"
            :style="{ backgroundImage: 'url(' + playButton + ')' }"
            @click="startVideo"
          ></button>
        </div>
        <div class="leftGroupText" v-if="isActive">
          <div class="name" v-if="!entertain.name.isEmpty()">
            {{ entertain.name }}
          </div>
          <div
            class="description"
            :class="{ extended: extended }"
            v-if="!entertain.description.isEmpty()"
          >
            {{ entertain.description }}
          </div>
          <div class="storer" v-if="!entertain.storer.isEmpty()">
            <h1>
              {{ entertain.storer }}
            </h1>
          </div>
        </div>
        <div class="rightGroupText" v-if="isActive">
          <div class="actorsTitle" v-if="!entertain.actorsTitle.isEmpty()">
            {{ entertain.actorsTitle }}
          </div>
          <div class="actors" v-if="!entertain.actors.isEmpty()">
            {{ entertain.actors }}
          </div>
        </div>
      </div>

      <swiper-slide class="slide1"> </swiper-slide>
      <swiper-slide class="slide2"> </swiper-slide>
    </swiper>
    <!-- <OfflineVideoPlayer :close="() => (this.playVideo = false)" v-if="isActive && playVideo"/> -->
  </div>
</template>

<script>
// import $ from "jquery";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";
// import OfflineVideoPlayer from "./OfflineVideoPlayer.vue";

export default {
  name: "Movie",
  components: {
    Swiper,
    SwiperSlide,
    // OfflineVideoPlayer
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  props: {
    entertain: Object,
    playButton: String,
    extended: Boolean,
    isActive: Boolean,
    startVideo: Function,
  },
  data() {
    var _this = this;
    return {
      swiperPos: false,
      // playVideo: false,
      swiperOption: {
        speed: 600,
        parallax: true,
        direction: "vertical",
        on: {
          slideChange: function () {
            var index = _this.swiper.activeIndex;
            if (index == 0) {
              _this.swiperPos = false;
            } else {
              _this.swiperPos = true;
            }

            console.log("_this.swiperPos: ", _this.swiperPos);
          },
        },
      },
    };
  },
  methods: {
    // handelPlayVideo() {
    //   this.playVideo = true;
    // },
  },
};
</script>

