        v-for="(mainButton, idx) in mainButtons"
        :key="mainButton.id"
        @click="handelSelect(idx)"
        class="buttonContainer"
        :changeActiveButton="onChangeActiveButton"
        :class="{ active: activeButton === idx }"
      >
        <div class="buttonBackground">
