<script>
// import { Swiper, SwiperSlide, directive } from "vue-awesome-swiper";
// import "swiper/css/swiper.css";
import $ from "jquery";

export default {
  name: "navbar",
  components: {
    // Swiper,
    // SwiperSlide,
  },
  // directives: {
  //   swiper: directive,
  // },
  props: {
    onSelect: Function,
    mainButtons: Array,
    activeButton: Number,
  },
  mounted() {
    // this.isLoaded = true;
    this.startMenuAnimation(100, true);
    // eventBus.$on("mainButtonEnableEvent", () => {
    //   this.isDisabled = false;
    // });
  },
  data() {
    return {
      width: window.innerWidth,
      // mainButtonIndex: 0,
    };
  },
  methods: {
    handelSelect(idx) {
      // this.mainButtonIndex = idx;
      this.activeButton = idx;
      this.onSelect(idx);
    },
    onChangeActiveButton() {
      // this.mainButtonIndex = idx;

      if (this.mainButtons.length - 1 > this.mainButtonIndex) {
        this.mainButtonIndex += 1;
      } else {
        this.mainButtonIndex = 0;
      }
      // this.onSelect(this.mainButtonIndex);
    },
    startMenuAnimation: function (start) {
      var type = 1, //circle type - 1 whole, 0.5 half, 0.25 quarter
        radius = "250px", //distance from center
        // start = 210, //shift start from 0
        $elements = $(".circleCon>.buttonContainer"),
        numberOfElements = type === 1 ? $elements.length : $elements.length - 1, //adj for even distro of elements when not full circle
        slice = (360 * type) / (numberOfElements * 2 - 1);
      if (this.width == 1334) {
        (type = 1), //circle type - 1 whole, 0.5 half, 0.25 quarter
          (radius = "285px"), //distance from center
          // start = 210, //shift start from 0
          ($elements = $(".circleCon>.buttonContainer")),
          (numberOfElements =
            type === 1 ? $elements.length : $elements.length - 1), //adj for even distro of elements when not full circle
          (slice = (360 * type) / (numberOfElements * 2 - 1));
        // console.log(this.width);
      }

      $elements.each(function (i) {
        var $self = $(this),
          rotate = slice * i + start,
          rotateReverse = rotate * -1;
        $self.css({
          transform:
            "rotate(" +
            rotate +
            "deg) translate(" +
            radius +
            ") rotate(" +
            rotateReverse +
            "deg)",
          opacity: 1,
        });
      });
      // this.isAnimButtonsElements = true;
      // setTimeout(() => {
      //   // this.isAnimButtonsElements = bol;
      //   if (this.animateClass != "") {
      //     this.hideBackgroundClass = "hideBackground";
      //   }
      //   //   $(".circleCenterCon").fadeIn(500);
      //   //   $(".circleCenterCon>p").addClass("startAnimat");
      //   //   $(".buttonContainerText").fadeIn(500);
      //   if (cb) cb();
      // }, 700);
    },
  },
};
</script>

